import React, { Fragment } from 'react';

import useWindowSize from 'react-use/lib/useWindowSize';
import Confetti from 'react-confetti';

const Result = ({ result, price }) => {
    const { width, height } = useWindowSize();
    const onPlayAgainClick = () => {
        window.location.href = "/";
    }
    return (
        <Fragment>
            {result
                ? <Fragment>
                    <Confetti
                        width={width}
                        height={height}
                    />
                    <Confetti style={{ paddingLeft: '50%' }}
                        width={'1000px'}
                        height={height}
                    />
                    <div className="custom-modal">
                        <img alt="img" className="image" width="50%" src="https://media.giphy.com/media/w8fQyOreoWYYFxEVIX/giphy.gif"></img>
                        <span>Congrats! <i class="far fa-smile-beam"></i> </span>
                        <span> You Have Won {price} Points </span>
                        <span className="close-btn btn btn-warning" onClick={onPlayAgainClick}>Play Again</span>
                    </div>
                </Fragment>
                : <div className="custom-modal">
                    <img alt="img" className="image" width="50%" src="https://media.giphy.com/media/5kFyQOwj9uqdgdovFf/giphy.gif"></img>
                    <span>So Sad <i class="far fa-frown"></i> </span>
                    <span> {price} </span>
                    <span className="close-btn btn btn-warning" onClick={onPlayAgainClick}>Play Again</span>
                </div>}




        </Fragment >
    )
}

export default Result;