import React, { useState } from 'react'

import WheelComponent from 'react-wheel-of-prizes'
import 'react-wheel-of-prizes/dist/index.css'
import Result from './Components/Result';
import './App.css';

const App = () => {
  const [showResult, setResult] = useState(false);
  const [winnerPrice, setWinnerPrice] = useState('');
  const [finalResult, setFinalResult] = useState(false);

  const segments = ['Better Luck Next Time',
    '70 Points',
    'Better Luck Next Time',
    '10  Points',
    'Better Luck Next Time',
    '26 Points',
    '111 Points',
    'Better Luck Next Time',
    '999 Points'];
  const segColors = [
    "#EE4040",
    "#F0CF50",
    "#815CD1",
    "#3DA5E0",
    "#34A24F",
    "#F9AA1F",
    "#EC3F3F",
    "#FF9000",
  ];
  const onFinished = (winner) => {
    if (winner !== "Better Luck Next Time") {
      setFinalResult(true);
    } else {
      setFinalResult(false);
    }
    setWinnerPrice(winner);
    setResult(true);
  }
  return (
    <div className="container">
      {showResult && <Result result={finalResult} price={winnerPrice} />}
      <WheelComponent
        segments={segments}
        segColors={segColors}
        onFinished={(winner) => onFinished(winner)}
        primaryColor='black'
        contrastColor='white'
        buttonText='Spin'
        isOnlyOnce={false} />
    </div>
  )
}

export default App;